.header {
  margin-top: .5rem;
  padding-left: 10px;
}

.header ul li {
  list-style: none;
  margin: 0;
  padding: .5rem 1rem;
  border-radius: 3px;
  color: #00bc8c;
}
.header ul {
  flex-wrap: wrap;
}

.header ul li:hover {
  background-color: #444;
}

.header a:hover {
  text-decoration: none;
}

.header h3{
  color: #fff;
}

.header h3:hover {
  color: #444;
}

.border-none:hover{
  background-color: #222222 !important;
}


